@import 'src/variables';

html {
  font-family: $family-default;
  line-height: 1.2;
}

body {
  background: linear-gradient(180.96deg, #0D1E48 -0.67%, #304571 100.39%);
  color: $text-color;
}

.header__container, .app__container, .footer__container {
  //max-width: $container-max-width;
  margin: 0 auto;
  padding: 0 $spacing-10;

  @media (min-width: $viewport-m) {
    padding: 0 $spacing-15;
  }

  @media (min-width: $viewport-l) {
    padding: 0 $spacing-40;
  }
}

.section-spacing {
  margin-bottom: $spacing-25;

  @media (min-width: $viewport-m) {
    margin-bottom: $spacing-27;
  }

  @media (min-width: $viewport-l) {
    margin-bottom: $spacing-30;
  }
}