@import "src/variables";

.nav-menu {

  &__link {
    font-size: var(--nav-link-size);
  }

  /* Burger Button */
  &__burger {
    position: relative;
    width: 22.5px;
    height: 16px;
    background: transparent;
    cursor: pointer;
    display: block;
  }

  &__checkbox {
    display: none;
  }

  &__line {
    display: block;
    position: absolute;
    height: 2px;
    width: 100%;
    background: white;
    border-radius: 9px;
    opacity: 1;
    left: 0;
    transform: rotate(0deg);
    transition: .25s ease-in-out;
  }

  &__line:nth-of-type(1) {
    top: 0;
    transform-origin: left center;
  }

  &__line:nth-of-type(2) {
    top: 50%;
    transform: translateY(-50%);
    transform-origin: left center;
  }

  &__line:nth-of-type(3) {
    top: 100%;
    transform-origin: left center;
    transform: translateY(-100%);
  }

  &__checkbox:checked + &__burger .nav-menu__line:nth-of-type(1) {
    transform: rotate(45deg);
    top: 0;
    left: 0;
  }

  &__checkbox:checked + &__burger .nav-menu__line:nth-of-type(2) {
    width: 0;
    opacity: 0;
  }

  &__checkbox:checked + &__burger .nav-menu__line:nth-of-type(3) {
    transform: rotate(-45deg);
    top: 16px;
    left: 0;
  }

  /* Navigation Menu */
  &__checkbox:checked ~ &__list {
    height: auto;
    max-height: 100vh;
  }

  &__list {
    flex-direction: column;
    display: block;
    align-items: center;
    position: absolute;
    top: var(--header-height);
    left: 0;
    width: 100%;
    background-color: $secondary-color;
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.2s ease-in-out;
  }

  &__item {
    display: flex;
    justify-content: center;
    padding: $spacing-10 0;
    background: transparent;

    &:hover {
      background: linear-gradient(98.25deg, #EBFF00 18.56%, #FF70D9 76.02%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      color: transparent;
    }
  }

  &__item:last-child {
    padding-bottom: $spacing-20;
  }

  @media (min-width: $viewport-m) {
    &__burger {
      display: none;
    }

    &__list {
      column-gap: $spacing-30;
      max-height: none;
      height: auto;
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: center;
      position: static;
      background-color: transparent;
      padding: 0;
    }

    &__item {
      padding: 0!important;
    }
  }
}