@import "src/variables";

.button {
  font-family: $family-default;
  font-weight: $font-weight-default;
  font-size: var(--hero-button-size);
  padding: $spacing-4 $spacing-20;
  border-radius: 100px;
  cursor: pointer;
  transition: $transition-default;
  color: $highlight-secondary-color;
  border: 2px solid $highlight-secondary-color;
  white-space: nowrap;

  &:hover {
    border-color: rgba($highlight-secondary-color, 0.6);
    color: rgba($highlight-secondary-color, 0.6);
  }

  &--filled {
    border: none;
    color: $secondary-color;
    background-color: $highlight-secondary-color;

    &:hover {
      background-color: rgba($highlight-secondary-color, 0.6);
      color: $secondary-color;
    }
  }

  @media (min-width: $viewport-m) {
    &--filled {
      padding: $spacing-7 $spacing-20;
    }
  }

  @media (min-width: $viewport-l) {
    padding: $spacing-7 $spacing-20;
  }
}