@import "src/variables";

.card {
  display: flex;
  align-items: flex-end;
  border-radius: $card-radius;
  padding: $spacing-17;
  height: var(--card-height);
  background: linear-gradient(-90deg, rgba(0, 0, 0, 0) -0.52%, rgba(15, 20, 32, 0.79) 99.48%), var(--card-background) center / cover no-repeat;
  background-size: cover;

  &__content {
    display: flex;
    flex-direction: column;
    row-gap: $spacing-11;
  }

  &__title {
    font-size: var(--card-title-size);
    font-weight: $font-weight-bold;
    line-height: 1;

    @media (min-width: $viewport-m) {
      max-width: 280px;
    }
  }

  &__subtitle {
    font-size: var(--card-subtitle-size);
    font-weight: $font-weight-bold;
  }

  &__button {
    align-self: start;
    font-size: var(--card-button-size);
  }
}

.card:nth-child(1) .card__title,
.card:nth-child(4) .card__title {
  @media (min-width: $viewport-l) {
    max-width: 650px;
    font-size: var(--card-title-size-l);
  }
}

.card:nth-child(1) .card__subtitle,
.card:nth-child(4) .card__subtitle {
  @media (min-width: $viewport-l) {
    font-size: var(--card-subtitle-size-l);
  }
}