//fonts
@import url('https://fonts.googleapis.com/css2?family=Lato:wght@700;900&display=swap');

$family-default: 'Lato', sans-serif;
$font-weight-default: 700;
$font-weight-bold: 900;


//viewports
$viewport-s: 361px;
$viewport-m: 769px;
$viewport-l: 1249px;

//colors
$primary-color: #304571;
$secondary-color: #000000;
$text-color: #FFFFFF;
$highlight-primary-color: #FF70D9;
$highlight-secondary-color: #EBFF00;

//sizes
$container-max-width: 1248px;
$card-radius: 10px;
:root {
  font-size: 16px;
  --hero-title-size: 2.25rem;
  --hero-subtitle-size: 1rem;
  --hero-button-size: 1rem;
  --header-height: 48px;
  --logo-width: 71px;
  --nav-link-size: 1.125rem;
  --card-height: 350px;
  --card-title-size: 2.5rem;
  --card-subtitle-size: 1rem;
  --card-button-size: 1rem;
  --section-title-size: 1.125rem;
  --text-section-title-size: 1.125rem;
  --text-section-text-size: 0.875rem;
  --footer-height: 127px;
  --footer-text-size: 1.125rem;
}

@media (min-width: $viewport-m) {
  :root {
    --hero-title-size: 3rem;
    --hero-subtitle-size: 1.375rem;
    --hero-button-size: 1.375rem;
    --header-height: 80px;
    --section-title-size: 1.5rem;
    --text-section-title-size: 1.5rem;
    --text-section-text-size: 1rem;
  }
}

@media (min-width: $viewport-l) {
  :root {
    --hero-title-size: 4.5rem;
    --hero-subtitle-size: 2rem;
    --logo-width: 104px;
    --section-title-size: 2rem;
    --card-title-size: 2.875rem;
    --card-subtitle-size: 1.5rem;
    --card-title-size-l: 4.625rem;
    --card-subtitle-size-l: 1.625rem;
    --text-section-title-size: 2rem;
    --text-section-text-size: 1.25rem;
  }
}

//mixins
@mixin flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

@mixin flex-between {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

//spacing
$spacing-unit: 2px;

$spacing-1: $spacing-unit * 1; // 2px
$spacing-2: $spacing-unit * 2; // 4px
$spacing-3: $spacing-unit * 3; // 6px
$spacing-4: $spacing-unit * 4; // 8px
$spacing-5: $spacing-unit * 5; // 10px
$spacing-6: $spacing-unit * 6; // 12px
$spacing-7: $spacing-unit * 7; // 14px
$spacing-8: $spacing-unit * 8; // 16px
$spacing-9: $spacing-unit * 9; // 18px
$spacing-10: $spacing-unit * 10; // 20px
$spacing-11: $spacing-unit * 11; // 22px
$spacing-12: $spacing-unit * 12; // 24px
$spacing-15: $spacing-unit * 15; // 30px
$spacing-17: $spacing-unit * 17; // 34px
$spacing-20: $spacing-unit * 20; // 40px
$spacing-22: $spacing-unit * 22; // 44px
$spacing-25: $spacing-unit * 25; // 50px
$spacing-27: $spacing-unit * 27; // 54px
$spacing-30: $spacing-unit * 30; // 60px
$spacing-40: $spacing-unit * 40; // 80px

