@import "src/variables";

.text-content {
  &__title {
    font-size: var(--text-section-title-size);
    font-weight: $font-weight-bold;
    margin-bottom: $spacing-10;

    @media (min-width: $viewport-m) {
      margin-bottom: $spacing-7;
    }

    @media (min-width: $viewport-l) {
      margin-bottom: $spacing-9;
    }
  }

  &__text {
    font-size: var(--text-section-text-size);
    font-weight: $font-weight-default;
    opacity: 0.6;
    margin-bottom: $spacing-10;
    // read-more
    display: -webkit-box;
    -webkit-line-clamp: 6;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    max-height: calc(1.2em * 6);

    @media (min-width: $viewport-l) {
      margin-bottom: $spacing-9;
    }
  }

  &__expanded {
    display: block;
    max-height: none;
  }

  &__link {
    font-size: var(--text-section-text-size);
    color: $highlight-secondary-color;
    background: transparent;

    &:hover {
      background: linear-gradient(98.25deg, #EBFF00 18.56%, #FF70D9 76.02%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      color: transparent;
    }
  }
}