.gradient-icon {
  display: inline-block;
  width: 25px;
  height: 21px;
  mask: url(#cart-mask) no-repeat center;
  -webkit-mask: url(#cart-mask) no-repeat center;
  mask-size: cover;
  -webkit-mask-size: cover;
  background: white; /* Default color */

  &:hover {
    background: linear-gradient(98.25deg, #EBFF00 18.56%, #FF70D9 76.02%);
  }
}
