@import "src/variables";

.banner {
  @include flex-center;
  height: 100vh;
  position: relative;
  background: linear-gradient(-90deg, rgba(0, 0, 0, 0) -0.52%, rgba(15, 20, 32, 0.79) 99.48%), url($banner-image), center;
  background-size: cover;
  overflow: hidden;

  &__content {
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    width: 100%;
    max-height: 100%;
    padding-bottom: $spacing-22;

    &-text {
      display: flex;
      flex-direction: column;
      gap: $spacing-11;
    }

    &-image {
      display: flex;
      position: relative;
      transform: rotate(0deg);
      transition: transform 0.8s ease-in-out;

      &:hover {
        transform: rotate(-4deg) scale(1.03);
      }
    }
  }

  &__title {
    font-size: var(--hero-title-size);
    font-weight: $font-weight-bold;
    text-align: center;
    line-height: 1;
    white-space: pre-line;

    &-highlight {
      color: $highlight-primary-color;
    }
  }

  &__subtitle {
    font-size: var(--hero-subtitle-size);
    font-weight: $font-weight-bold;
    text-align: center;

    &-highlight {
      color: $highlight-secondary-color;
    }
  }

  &__button {
    font-size: var(--hero-button-size);
    margin: auto;
    width: auto;
    align-self: flex-start;
  }

  @media (min-width: $viewport-m) {
    &__content {
      flex-direction: row;
      @include flex-between;

      &-image {
        top: $spacing-12;
      }
    }

    &__title, &__subtitle {
      text-align: left;
    }

    &__button {
      margin: 0;
    }
  }
}