@import "src/variables";

.header {
  @include flex-center;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 1;
  background-color: $secondary-color;
  height: var(--header-height);

  &__container {
    @include flex-between;
    flex-direction: row;
    width: 100%;
  }

  &__burger {
    display: block;
    background: none;
    border: none;
    font-size: 2rem;
    cursor: pointer;
  }
}