@import "src/variables";

.cards-section {

  &__title {
    font-size: var(--section-title-size);
    margin-bottom: $spacing-15;

    @media (min-width: $viewport-l) {
      margin-bottom: $spacing-20;
    }
  }

  &__cards {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: auto;
    gap: $spacing-15;
    grid-template-areas:
        "card1"
        "card2"
        "card3"
        "card4";

    @media (min-width: $viewport-m) {
      grid-template-columns: 1fr 1fr;
      grid-template-rows: auto auto;
      grid-template-areas:
        "card1 card2"
        "card3 card4";
    }

    @media (min-width: $viewport-l) {
      gap: $spacing-20;
      grid-template-columns: 1fr 1fr;
      grid-template-areas:
        "card1 card1"
        "card2 card3"
        "card4 card4";
    }
  }

  &__card:nth-child(1) {
    grid-area: card1;
  }

  &__card:nth-child(2) {
    grid-area: card2;
  }

  &__card:nth-child(3) {
    grid-area: card3;
  }

  &__card:nth-child(4) {
    grid-area: card4;
  }
}